<template>
    <!-- Contact -->
    <div id="contact" v-if="infos.profile">
        <div id="map" map></div>
        <!-- Contact Info -->
        <div class="contact-info">
            <h3 class="main-heading"><span>{{ $t('contact') | capitalize }}</span></h3>
            <ul>
                <li>{{ infos.profile.address }}<br/><br/></li>
                <li>{{ $t('email') | capitalize }}: {{ infos.profile.email }}</li>
                <li>{{ $t('phone') | capitalize }}: {{ infos.profile.phone }}</li>
            </ul>

            <div class="social">
            </div>
        </div>
        <!-- /Contact Info -->
        <!-- Contact Form -->
        <div class="contact-form">
            <h3 class="main-heading"><span>{{ $t('lets_keep_in_touch') | capitalize }}</span></h3>
            <div id="contact-status"></div>
            <form name="form" id="contactform" @submit.prevent="register" @keydown="form.onKeydown($event)" novalidate>
                <p>
                    <label for="name">{{ $t('your_name') | capitalize }}</label>
                    <input type="text" id="name" name="name" class="input"
                           maxlength="100"
                           v-model="form.name"
                           v-validate="'required'"
                           :class="{ 'is-invalid': form.errors.has('name')|errors.has('name') }">
                    <app-error :form="form" :error="errors" field="name"/>
                </p>
                <p>
                    <label for="email">{{ $t('your_email') | capitalize }}</label>
                    <input type="email" id="email" name="email" class="input"
                           maxlength="100"
                           v-model="form.email"
                           v-validate="'required|email'"
                           :class="{ 'is-invalid': form.errors.has('email')|errors.has('email') }">
                    <app-error :form="form" :error="errors" field="email"/>
                </p>
                <p>
                    <label for="message">{{ $t('your_message') | capitalize }}</label>
                    <textarea id="message" name="message" cols="88" rows="6" class="textarea"
                              maxlength="500"
                              v-model="form.message"
                              v-validate="'required'"
                              :class="{ 'is-invalid': form.errors.has('message')|errors.has('message') }"></textarea>
                    <app-error :form="form" :error="errors" field="message"/>
                </p>
                <v-button :loading="form.busy" name="submit" class="submit">
                    {{ $t('send_message') | capitalize }}
                </v-button>
            </form>
        </div>
        <!-- /Contact Form -->
    </div>
    <!-- /contact -->
</template>

<script>
    import Form from 'vform';
    import swal from 'sweetalert2';
    import CONFIG from "../config";
    export default {
        metaInfo () {
            return { title: this.$options.filters.capitalize(this.$t('contact')) }
        },
        data: () => ({
            form: new Form({
                name: '',
                email: '',
                message: ''
            })
        }),
        methods: {
            register(e) {
                e.preventDefault();
                this.$validator.validateAll().then(async (result) => {
                    if (result) {
                        const {data:{data}} = await this.form.post(CONFIG.API_URL_ROOT + 'contact');
                        console.log(data);
                        if (data.name) {
                            swal({
                                title: this.$t('Attention'),
                                html: this.$t('success_contact_form').replace('<name>', data.name),
                                confirmButtonText: this.$options.filters.capitalize(this.$t('continue')),
                                type: 'success'
                            });
                        }
                        return this.clearForm();
                    }
                });

            },
            clearForm() {
                this.form.reset();
                const self = this;
                setTimeout(function () {
                    self.errors.clear();
                }, 1);
            }
        },
        computed: {
            infos: function () {
                let infos = this.$store.getters['infos/infos'],
                    computedInfos = {};
                for(let idx in infos) {
                    if (infos.hasOwnProperty(idx)) {
                        let info = infos[idx];
                        if(!computedInfos.hasOwnProperty(info.type)) {
                            computedInfos[info.type] = {};
                        }
                        computedInfos[info.type][info.key] = info.value;
                    }
                }
                return computedInfos;
            }
        },
        created() {
            this.$store.dispatch('infos/fetchAllInfos');
        }
    }
</script>


