import client from "../client";
import * as types from "../mutation-types";

// state
export const state = {
    projects: null
};

// getters
export const getters = {
    projects: state => state.projects
};

// mutations
export const mutations = {
    [types.FETCH_PROJECTS_SUCCESS](state, {projects}) {
        state.projects = projects
    },
    [types.FETCH_PROJECTS_FAILURE] (state) {
        state.projects = null
    }
};

// actions
export const actions = {
    async fetchAllProjects({commit}) {
        try {
            const {data: {data}} = await client.get("projects");
            commit(types.FETCH_PROJECTS_SUCCESS, {projects: data});
        } catch (e) {
            commit(types.FETCH_PROJECTS_FAILURE);
        }
    }
};
