<script>
    export default {
        nome: "Bottom",
        data: () => ({
            version: '--VERSION--',
            year: new Date().getFullYear(),
            social: {
                facebook: 'https://www.facebook.com/MarceloRodovalhoDeveloper',
                linkedin: 'https://br.linkedin.com/in/marcelorodovalho',
                google: 'https://plus.google.com/+MarceloRodovalho',
                whatsapp: 'https://api.whatsapp.com/send?phone=5561991852247',
                telegram: 'https://t.me/se45ky',
                skype: 'https://join.skype.com/invite/IQ0R8sgUdtc2'
            }
        }),
        computed: {
            infos: function () {
                let infos = this.$store.getters['infos/infos'],
                    computedInfos = {};
                for(let idx in infos) {
                    if (infos.hasOwnProperty(idx)) {
                        let info = infos[idx];
                        if(!computedInfos.hasOwnProperty(info.type)) {
                            computedInfos[info.type] = {};
                        }
                        computedInfos[info.type][info.key] = info.value;
                    }
                }
                return computedInfos;
            }
        },
        created() {
            this.$store.dispatch('infos/fetchAllInfos');
        }
    }
</script>