import client from "../client";
import * as types from "../mutation-types";

// state
export const state = {
    education: null
};

// getters
export const getters = {
    education: state => state.education
};

// mutations
export const mutations = {
    [types.FETCH_EDUCATION_SUCCESS](state, {education}) {
        state.education = education
    },
    [types.FETCH_EDUCATION_FAILURE] (state) {
        state.education = null
    }
};

// actions
export const actions = {
    async fetchAllEducation({commit}) {
        try {
            const {data: {data}} = await client.get("education");
            commit(types.FETCH_EDUCATION_SUCCESS, {education: data});
        } catch (e) {
            commit(types.FETCH_EDUCATION_FAILURE);
        }
    }
};
