import client from "../client";
import * as types from "../mutation-types";

// state
export const state = {
    experiences: null
};

// getters
export const getters = {
    experiences: state => state.experiences
};

// mutations
export const mutations = {
    [types.FETCH_EXPERIENCES_SUCCESS](state, {experiences}) {
        state.experiences = experiences
    },
    [types.FETCH_EXPERIENCES_FAILURE] (state) {
        state.experiences = null
    }
};

// actions
export const actions = {
    async fetchAllExperiences({commit}) {
        try {
            const {data: {data}} = await client.get("experiences");
            commit(types.FETCH_EXPERIENCES_SUCCESS, {experiences: data});
        } catch (e) {
            commit(types.FETCH_EXPERIENCES_FAILURE);
        }
    }
};
