<template>
    <div class="help-block invalid-feedback" v-if="error.has(field)" v-html="error.first(field)"/>
    <div class="help-block invalid-feedback" v-else-if="form.errors.has(field)" v-html="form.errors.get(field)"/>
</template>

<script>
    export default {
        name: 'app-error',
        props: {
            form: {
                type: Object,
                required: true
            },
            error: {
                type: Function|Object,
                required: true
            },
            field: {
                type: String,
                required: true
            }
        }
    }
</script>