import client from "../client";
import * as types from "../mutation-types";

// state
export const state = {
    infos: null
};

// getters
export const getters = {
    infos: state => state.infos
};

// mutations
export const mutations = {
    [types.FETCH_INFOS_SUCCESS](state, {infos}) {
        state.infos = infos
    },
    [types.FETCH_INFOS_FAILURE] (state) {
        state.infos = null
    }
};

// actions
export const actions = {
    async fetchAllInfos({commit}) {
        try {
            const {data: {data}} = await client.get("infos");
            commit(types.FETCH_INFOS_SUCCESS, {infos: data});
        } catch (e) {
            commit(types.FETCH_INFOS_FAILURE);
        }
    }
};
