<template>
    <!-- Portfolio -->
    <div id="portfolio">
        <ul id="portfolio-filter" v-if="tags">
            <li v-for="tag in tags" class="button" v-if="tag" @click="filter(tag.slug)" :key="tag.slug">
                <a :class="{current: tag.slug===filterOption}">{{ tag.name }}</a>
            </li>
        </ul>
        <div class="extra-text">{{ $t('worked_projects') | capitalize }}</div>
        <isotope id="portfolio-list" ref="cpt" :item-selector="'project-item'" :list="projects" :options='option'
                 @filter="option.filterOption=arguments[0]" v-images-loaded:on.progress="layout">
            <div v-for="project in projects" :class="Object.keys(project.fields).join(' ')" :key="project.id"
                 class="view view-effect">
                <a href="#lightbox" :title="project.name"
                   v-on:click="setProject(project)"
                   class="folio" :class="'fancybox.'+project.type+' '+project.type" fancybox
                   :fancybox-type="project.type">
                    <span class="img" :style="{'background-image':'url('+project.image+')'}"></span>
                    <div class="mask"></div>
                    <div class="content">
                        <h2>{{ project.name }}</h2>
                        <p>{{ Object.values(project.skills).join(' / ') }}</p>
                        <strong class="info">© {{ project.customer }}</strong>
                        <strong class="info">© {{ project.company }}</strong>
                    </div>
                </a>
            </div>
        </isotope>
        <div v-if="project" class="lightbox-target" id="lightbox">
            <img :src="project.image">
            <a class="lightbox-close" href="#" v-on:click="setProject(null)"></a>
            <a class="lightbox-link" :href="project.url" target="_blank">{{ $t('see_live_version') | capitalize }}</a>
        </div>
    </div>
    <!-- /Portfolio -->
</template>

<script>
    import isotope from 'vueisotope';
    import imagesLoaded from 'vue-images-loaded';

    let filterOption;
    export default {
        directives: {
            imagesLoaded
        },
        components: {
            isotope,
        },
        metaInfo() {
            return {title: this.$options.filters.capitalize(this.$t('portfolio'))}
        },
        created() {
            this.$store.dispatch('projects/fetchAllProjects');
            this.$store.dispatch('tags/cloud');
        },
        computed: {
            projects: function () {
                return this.$store.getters['projects/projects'];
            },
            tags: function () {
                let tags = [
                        {
                            name: 'All',
                            slug: 'all'
                        }
                    ],
                    tagsAll = this.$store.getters['tags/tags'];
                return tags.concat(tagsAll);
            }
        },
        methods: {
            layout() {
                this.$refs.cpt.layout('masonry');
            },
            filter(key) {
                this.filterOption = filterOption = key;
                this.$refs.cpt.filter(key === 'all' ? 'all' : 'patternFilter');
            },
            setProject(project) {
                this.project = project;
            },
            getProject() {
                return this.project;
            }
        },
        data: () => ({
            project: null,
            filterOption: 'all',
            option: {
                filterOption: "all",
                itemSelector: ".project-item",
                getFilterData: {
                    "all": function () {
                        return true;
                    },
                    patternFilter: function (el) {
                        return Object.keys(el.fields).find(element => element === filterOption);
                    }
                }
            }
        })
    }
</script>