import Vue from 'vue'
import store from './store'
import router from './router'
import i18n from './plugins/i18n'
import Translate from './plugins/translate'
import App from './components/App.vue'

import './plugins'
import './components'

require('reset-css');
require('animate.css');
require('hover.css');
require('./assets/css/app.css');

import Tooltip from 'bootstrap-vue/es/components/tooltip/tooltip'
import TooltipDirective from 'bootstrap-vue/es/directives/tooltip/tooltip'
import Vuebar from 'vuebar';
import VeeValidate, {Validator} from "vee-validate";
import messagesBr from "vee-validate/dist/locale/pt_BR";
import messagesEn from "vee-validate/dist/locale/en";
import AppError from './components/Form/AppError.vue';

const messagesLocales = {
    en: messagesEn,
    pt_BR: messagesBr
};
const currentLocale = store.getters['lang/currentLocale'];

Validator.localize(currentLocale, messagesLocales[currentLocale]);

Vue.use(VeeValidate, {
    locale: currentLocale,
    dictionary: {
        pt_BR: {messages: messagesBr},
        en: {messages: messagesEn},
    }
});

Vue.use(Translate);
Vue.use(Vuebar);
Vue.component(AppError.name, AppError);
Vue.component('b-tooltip', Tooltip);
Vue.directive('b-tooltip', TooltipDirective);

Vue.config.productionTip = false;

Vue.filter('capitalize', function (value) {
    if (!value) return '';
    value = value.toString();
    return value.charAt(0).toUpperCase() + value.slice(1)
});

Vue.filter('json', function (value) {
    return JSON.stringify(value, null, 2);
});

window.Vue = new Vue({
    i18n,
    store,
    router,
    ...App
});