<template>
    <div class="lang-menu">
      <a v-for="(value, key) in locales" href="#"
         :class="{active: locale === key}"
        @click.prevent="setLocale(key)" :key="key">
          {{ key.toUpperCase() }}
      </a>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { loadMessages } from '../plugins/i18n'

export default {
  computed: mapGetters({
    locale: 'lang/locale',
    locales: 'lang/locales'
  }),

  methods: {
    setLocale (locale) {
      if (this.$i18n.locale !== locale) {
        loadMessages(locale);
        this.$validator.localize(this.locales[locale]);
        this.$store.dispatch('lang/setLocale', { locale })
      }
    }
  }
}
</script>
