<template>
    <div v-if="infos.profile">
        <!-- About section -->
        <div class="about">
            <div class="photo-inner"><img src="../assets/images/photo.jpg" height="186" width="153" :alt="infos.profile.name"/></div>
            <h1>{{ infos.profile.abbr }}</h1>
            <h3>{{ infos.profile.title }}</h3>
            <p>{{ infos.profile | __t('description') }}</p>
        </div>
        <!-- /About section -->
        <!-- Personal info section -->
        <ul class="personal-info">
            <li><label>{{ $t('name') | capitalize }}</label><span>{{ infos.profile.name }}</span></li>
            <li><label>{{ $t('birthday') | capitalize }}</label><span>{{ infos.profile.birthday }}</span></li>
            <li><label>{{ $t('address') | capitalize }}</label><span>{{ infos.profile.address }}</span></li>
            <li><label>{{ $t('email') | capitalize }}</label><span>{{ infos.profile.email }}</span></li>
            <li><label>{{ $t('phone') | capitalize }}</label><span>{{ infos.profile.phone }}</span></li>
        </ul>
        <!-- /Personal info section -->
    </div>
</template>

<script>
    export default {
        metaInfo () {
            return { title: this.$options.filters.capitalize(this.$t('profile')) }
        },
        computed: {
            infos: function () {
                let infos = this.$store.getters['infos/infos'],
                    computedInfos = {};
                for(let idx in infos) {
                    if (infos.hasOwnProperty(idx)) {
                        let info = infos[idx];
                        if(!computedInfos.hasOwnProperty(info.type)) {
                            computedInfos[info.type] = {};
                        }
                        computedInfos[info.type][info.key] = info.value;
                    }
                }
                return computedInfos;
            }
        },
        created() {
            this.$store.dispatch('infos/fetchAllInfos');
        }
    }
</script>


