import client from "../client";
import * as types from "../mutation-types";

// state
export const state = {
    tags: null
};

// getters
export const getters = {
    tags: state => state.tags
};

// mutations
export const mutations = {
    [types.FETCH_TAGS_SUCCESS](state, {tags}) {
        state.tags = tags
    },
    [types.FETCH_TAGS_FAILURE] (state) {
        state.tags = null
    }
};

// actions
export const actions = {
    async fetchAllTags({commit}) {
        try {
            const {data: {data}} = await client.get("tags");
            commit(types.FETCH_TAGS_SUCCESS, {tags: data});
        } catch (e) {
            commit(types.FETCH_TAGS_FAILURE);
        }
    },
    async cloud({commit}) {
        try {
            const {data: {data}} = await client.get("tagscloud");
            commit(types.FETCH_TAGS_SUCCESS, {tags: data});
        } catch (e) {
            commit(types.FETCH_TAGS_FAILURE);
        }
    }
};
