import store from '../store';

const Translate = {};
Translate.install = function (Vue) {
    /**
     * @param {Object} values
     * @param {String} property
     */
    Vue.__t = function (values, property) {
        const locale = store.getters['lang/locale'];
        if (values.hasOwnProperty(property + '_' + locale)) {
            return values[property + '_' + locale];
        }
    };
    Vue.filter('__t', Vue.__t)
};

export default Translate;