import client from "../client";
import * as types from "../mutation-types";

// state
export const state = {
    skills: null
};

// getters
export const getters = {
    skills: state => state.skills
};

// mutations
export const mutations = {
    [types.FETCH_SKILLS_SUCCESS](state, {skills}) {
        state.skills = skills
    },
    [types.FETCH_SKILLS_FAILURE] (state) {
        state.skills = null
    }
};

// actions
export const actions = {
    async fetchAllSkills({commit}) {
        try {
            const {data: {data}} = await client.get("skills");
            commit(types.FETCH_SKILLS_SUCCESS, {skills: data});
        } catch (e) {
            commit(types.FETCH_SKILLS_FAILURE);
        }
    }
};
