<template>
    <!-- Resume -->
    <div id="resume">
        <div class="timeline-section">
            <!-- Timeline for Employment  -->
            <h3 class="main-heading"><span>{{ $t('experiences') | capitalize }}</span></h3>
            <ul class="timeline">
                <li v-for="(job, index) in experiences" :key="'job'+index">
                    <div class="timelineUnit">
                        <h3>{{ job | __t('title') }}<span class="timelineDate">{{ job.period }} | {{ job.time }}</span></h3>
                        <h4>{{ job.company }}</h4>
                        <h5>{{ job | __t('assignments') }}</h5>
                        <p v-if="job.customers"><b>{{ $t('customers') | capitalize }}:</b> {{ job.customers }}</p>
                        <p v-if="job.projects"><b>{{ $t('projects') | capitalize }}:</b> {{ job.projects }}</p>
                        <p><b>{{ $t('technologies') | capitalize }}:</b> {{ Object.values(job.skills).join(', ') }}</p>
                    </div>
                </li>
            </ul>
            <div class="clear"></div>
            <!-- /Timeline for Employment  -->
            <!-- Timeline for Education  -->
            <h3 class="main-heading"><span>{{ $t('education') | capitalize }}</span></h3>
            <ul class="timeline">
                <li v-for="(course, index) in education" :key="'course'+index">
                    <div class="timelineUnit">
                        <h4>{{ course | __t('course') }}<span class="timelineDate">{{ course.period }}</span></h4>
                        <h5>{{ course | __t('institution') }}</h5>
                        <p>{{ course | __t('description') }}</p>
                    </div>
                </li>
            </ul>
            <div class="clear"></div>
            <!-- /Timeline for Education  -->
        </div>
        <div class="skills-section">
            <!-- Skills -->
            <div v-for="(types, key, index) in skills" :key="'types'+key+index">
                <h3 class="main-heading"><span>{{ $t(key) | capitalize }}</span></h3>
                <ul class="skills">
                    <li v-for="(index2, skill) in types" :key="'skill-'+skill+'-'+index+'-'+index2">
                        <h4>{{ skill }}</h4>
                        <fa v-b-tooltip.hover :title="$t(skillsRange[i-1])" v-for="i in index2" :icon="['fas','check-circle']" fixed-width :key="'positiveRank-'+skill+'-'+i+'-'+index2+'-'+index"/>
                        <fa v-for="i in (skillsRange.length - index2)" :icon="['fas','times-circle']" fixed-width :key="'negativeRank-'+skill+'-'+i+'-'+index2+'-'+index"/>
                    </li>
                </ul>
            </div>
            <!-- /Skills -->
        </div>
    </div>
    <!-- /Resume -->
</template>

<script>
    export default {
        metaInfo() {
            return {title: this.$options.filters.capitalize(this.$t('resume'))}
        },
        created() {
            this.$store.dispatch('experiences/fetchAllExperiences');
            this.$store.dispatch('education/fetchAllEducation');
            this.$store.dispatch('skills/fetchAllSkills');
        },
        computed: {
            experiences: function () {
                return this.$store.getters['experiences/experiences'];
            },
            education: function () {
                return this.$store.getters['education/education'];
            },
            skills: function () {
                let skills = this.$store.getters['skills/skills'],
                    computedSkills = {};
                for (let idx in skills) {
                    if (skills.hasOwnProperty(idx)) {
                        let skill = skills[idx];
                        if (!computedSkills.hasOwnProperty(skill.category)) {
                            computedSkills[skill.category] = {};
                        }
                        computedSkills[skill.category][skill.name] = parseInt(skill.rank);
                    }
                }
                return computedSkills;
            },
        },
        data: () => ({
            skillsRange: [
                'skillsRank1',
                'skillsRank2',
                'skillsRank3',
                'skillsRank4',
                'skillsRank5'
            ]
        })
    }
</script>


